<template>
<div>
<!--  面包屑导航区域-->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    <el-breadcrumb-item>用户列表</el-breadcrumb-item>
  </el-breadcrumb>
<!--  卡片视图区-->
  <el-card >
<!--    添加与搜索区域-->
    <el-row :gutter="20">
      <el-col :span="10">
        <!--   搜索与添加区域-->
        <el-input placeholder="请输入内容"  v-model="quertInfo.query" clearable @clear="getUserList">
          <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
      </el-col>
    </el-row>

<!--    用户列表区域-->
      <el-table :data="userlist"
              stripe
              style="width: 100%" border>
        <el-table-column label="序号" type="index" width="60px"></el-table-column>
        <el-table-column prop="username" label="姓名" ></el-table-column>
        <el-table-column prop="email" label="邮箱" ></el-table-column>
        <el-table-column prop="mobile" label="电话" ></el-table-column>
        <el-table-column prop="role_name" label="角色" ></el-table-column>
        <el-table-column prop="mg_state" label="状态" >
          <template v-slot:default="{row}">
<!--            {{ row.mg_state}} ！！！重要的用法-->
            <el-switch v-model="row.mg_state" active-color="#13ce66"
                    inactive-color="#ff4949" @change="userStateChanged(row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column  label="操作" width="180px">
          <template v-slot:default="{row}">
<!--          修改-->
          <el-button @click="showEditDialog(row.id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
<!--          删除-->
          <el-button @click="removeById(row.id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
<!--          分配角色 加入了文字提示-->
          <el-tooltip  effect="dark" content="分配角色" placement="top" :enterable="false">
            <el-button type="warning" icon="el-icon-setting" size="mini" ></el-button>
          </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

<!--    分页区域-->
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="quertInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="quertInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
    </el-pagination>
  </el-card>

<!--  添加用户的对话框-->
  <el-dialog
          title="添加用户"
          :visible.sync="addDialogVisible"
          width="50%">
<!--    内容主题区域-->
    <el-form :model="addForm" :rules="addFormRules"
       ref="addFormRef" label-width="70px">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="手机" prop="mobile">
        <el-input v-model="addForm.mobile"></el-input>
      </el-form-item>
    </el-form>
<!--    底部区域-->
    <span slot="footer" class="dialog-footer">
<!--   按钮关闭属性   @click="addDialogVisible = false"-->
    <el-button @click="resetform">重置</el-button>
    <el-button type="primary" @click="addUser">确 定</el-button>
  </span>
  </el-dialog>

<!--  修改用户的对话框-->
  <el-dialog
          title="修改用户"
          :visible.sync="editDialogVisiblle"
          width="50%">
    <!--    内容主题区域-->
    <el-form :model="editForm" :rules="addFormRules"
             ref="editFormRef" label-width="70px">
      <el-form-item label="用户名" disabled>
        <el-input v-model="editForm.username"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="editForm.email"></el-input>
      </el-form-item>
      <el-form-item label="手机" prop="mobile">
        <el-input v-model="editForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <!--    底部区域-->
    <span slot="footer" class="dialog-footer">
<!--   按钮关闭属性   @click="addDialogVisible = false"-->
    <el-button @click="resetform">重置</el-button>
    <el-button type="primary" @click="editDialogVisiblle">确 定</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
  export default {
    name: "Users",
    data(){
      //验证邮箱校验规则
      let checkEmail=(rule,value,callback)=>{
        const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(regEmail.test(value)){
          //合法的邮箱
          return callback()
        }
        callback(new Error('请输入合法的邮箱'))
      }
      //验证手机号校验规则
      let checkMobile=(rule,value,callback)=>{
        // 验证手机号的正则表达式
        const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        //如果regMobile的test方法中value经过校验合法的手机号
        if (regMobile.test(value)) {
          //执行callback方法
          return callback()
        }

        callback(new Error('请输入合法的手机号'))

      }

      return{
      //获取用户列表的参数对象
        quertInfo:{
          query:'',
          //当前的页数
          pagenum:1,
          //当前每页显示多少条数据
          pagesize:2
        },
        userlist:[],
        total:0,
        //控制添加用户对话框的显示和隐藏
        addDialogVisible:false,
        //控制修改用户对话框的显示和隐藏
        editDialogVisiblle:false,
        //查询到的用户信息对象
        editForm:{},
        //添加用户的表单数据
        addForm:{
          username:'',
          password:'',
          email:'',
          mobile:''
        },
        //添加用户表单的规则对象
        addFormRules:{
          username:[
            {required: true, message: '请输入用户名', trigger: 'blur'},
            {min: 3, max: 10, message: '用户名长度在 3 到 10 个字符之间', trigger: 'blur'}
          ],
          password:[
            {required: true, message: '请输入密码', trigger: 'blur'},
            {min: 6, max: 15, message: '密码长度在 6到 15个字符之间', trigger: 'blur'}
          ],
          email:[
            {required: true, message: '请输入邮箱', trigger: 'blur'},
            { validator: checkEmail, trigger: 'blur' }
          ],
          mobile:[
            {required: true, message: '请输入手机号', trigger: 'blur'},
            { validator: checkMobile, trigger: 'blur' }
          ]
        },

      }
    },
    created() {
      this.getUserList()
    },
    methods:{
      getUserList(){
        this.$http.get('users',{params:this.quertInfo})
          .then(res=> {
            if (res.status!==200){
             return this.$message.error(res.data.meta.msg);
            }
            this.userlist=res.data.data.users
            // console.log(this.userlist);
            this.total=res.data.data.total

          })
      },
      //监听pagesize改变的事件
      handleSizeChange(newSize){
        // console.log(newSize)
        this.quertInfo.pagesize=newSize
        //重新获取数据
        this.getUserList()
      },
      //监听页码值改变的事件
      handleCurrentChange(newpage){
        console.log(newpage)
        this.quertInfo.pagenum=newpage
        //重新获取数据
        this.getUserList()
      },
      //监听switch开关状态的事件
      userStateChanged(userinfo){
        this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
          .then(res=> {
            if (res.data.meta.status !== 200) {
              userinfo.mg_state!=userinfo.mg_state
              return this.$message.error('更新用户状态失败');
            }
            this.$message.success('更新用户状态成功')
          })
          },
      //点击对话框里的重置按钮重置表单
      resetform(){
        this.$refs.addFormRef.resetFields()
      },
      //点击按钮，添加用户
      addUser(){
        this.$refs.addFormRef.validate((valid)=>{
          // console.log(valid)
          if (!valid) return
          //可以发起添加用户的请求

          this.$http.post('users',this.addForm)
            .then(res=> {
              console.log(this.addForm)
              console.log(res)
              if (res.data.meta.status !== 201) {
                return this.$message.error('添加用户失败');
              }
                this.$message.success('添加用户成功')
                //隐藏添加用户的对话框
                this.addDialogVisible=false
                //重新获取用户列表
                this.getUserList()


            })
        })
      },
      //展示编辑用户的对话框
      showEditDialog(id){
          console.log( id)
        this.$http.get('users/'+id)
          .then(res=> {
            console.log(res.data.data)
            if (res.data.meta.status !== 200) {
              return this.$message.error('查询用户信息失败');
            }
            this.editForm=res.data.data
            this.editDialogVisiblle=true
          })
      },
      //根据id删除对应的用户信息
      removeById(id){
        //弹框询问是否删除
          this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.delete(`users/`+id)
              .then(res=> {
                console.log(res)
                if (res.data.meta.status !== 200) {
                  return this.$message.error('删除用户失败');
                }
                this.$message.success('删除用户成功')
                //删除成功后刷新数据列表
                this.getUserList();
              })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
      }
    }
  }
</script>

<style scoped>

</style>