<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt="">
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <el-container>
      <!-- 侧边栏 -->
      <el-aside style="height: 580px" :width="isCollapse ? '64px':'200px'" >
        <div class="toggle-button"
               @click="toggleCollapse">|||</div>
        <el-menu background-color="#333744" unique-opened
                 text-color="#fff" active-text-color="#409EFF"
                 :collapse="isCollapse" :collapse-transition="false"
                 :router="true" :default-active="$route.path">
          <!-- 一级菜单 -->
          <el-submenu style="padding-left: 15px"  :index="item.id + ''" v-for="item in menulist" :key="item.id">
            <template slot="title">
              <i :class="iconsObj[item.id]"></i>
              <span style="padding-left: 10px">{{item.authName}}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item  :index=" '/' + subitem.path"  v-for="subitem in item.children" >
              <template  slot="title">
              <i class="el-icon-menu"></i>
              <span  slot="title">{{subitem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu >
        </el-menu>
      </el-aside>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  export default {
    name: "Home",
    data(){
      return{
        // 是否折叠
        isCollapse: false,
        menulist:[],
        iconsObj: {
          '125': 'iconfont icon-user',
          '103': 'iconfont icon-tijikongjian',
          '101': 'iconfont icon-shangpin',
          '102': 'iconfont icon-danju',
          '145': 'iconfont icon-baobiao'
        },
      }
    },
    //生命周期创建时请求菜单
    created() {
      this.getMenuList()
    },
    methods:{
      //退出功能
      logout(){
        window.sessionStorage.clear();
        this.$router.push('/login')
      },
      // 点击按钮，切换菜单的折叠与展开
      toggleCollapse(){
        this.isCollapse=!this.isCollapse
      },
      // 获取所有的菜单数据
      getMenuList(){
        this.$http.get('menus')
          .then(res=> {
            // console.log(res.data.data);
            if (res.status==200){
              // this.$message.success(res.data.meta.msg);
              this.menulist=res.data.data
            }else {
              this.$message.error(res.data.meta.msg);
            }
          })
      }
    }
  }
</script>

<style scoped>
.el-header {
  background-color: #373d41;
  display: flex;
  /*左右贴边对齐*/
  justify-content: center;
  color: #ffffff;
  font-size: 18px;
  position: relative;
  /*上下垂直居中*/
  align-items: center;
}
.el-header  div img{
     display: block;
     width: 60px;
     height: 60px;
     float: left;
  }
.el-header   div span{
     display: block;
     line-height: 60px;
     float: left;
     padding-left: 6px;
  }
.el-button{
  position: absolute;
  right: 10px;
}
.el-aside{
  background-color: #333744;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
.el-menu{
 border-right: 0px;
 overflow-x: hidden;
}
</style>